import React from 'react';
import {Card, CardHeader, CardContent, TextField, Select, MenuItem, OutlinedInput, InputLabel } from '@material-ui/core';
import SyntaxHighlighter from 'react-syntax-highlighter';

function InputCode(props) {
  var languages = SyntaxHighlighter.supportedLanguages;
  languages.push('');
  return (
    <Card>
      <CardHeader title="Set Sample Code"/>
      <CardContent>
        <InputLabel style={{ "fontSize":12 }}>language</InputLabel>
        <Select
          fullWidth={true}
          input={<OutlinedInput name="language" />}
          value={props.language} onChange={e => props.setLanguage(e.target.value)}
        >
          <MenuItem value={props.language}>
            <em>{props.language}</em>
          </MenuItem>
          {languages.map((language) =>
             <MenuItem value={language} key={language}>{language}</MenuItem>
          )}
        </Select>
        
        <TextField 
          label="code" fullWidth={true} margin="normal" variant="outlined" multiline
          value={props.code} onChange={e => props.setCode(e.target.value)} 
        />
      </CardContent>
    </Card>
  )
}

export default InputCode;
